import { Component, OnInit } from '@angular/core';
import { AuthService } from '../servicios/auth.service';
import { DatosService } from '../servicios/datos.service';
import { ModalController } from '@ionic/angular';
import { DatoComponent } from '../componentes/dato/dato.component';
import { ActionSheetController } from '@ionic/angular';
import { ActivatedRoute } from '@angular/router';


@Component({
  selector: 'app-home',
  templateUrl: 'home.page.html',
  styleUrls: ['home.page.scss'],
})
export class HomePage implements OnInit{

  public datosFire :any = [] ;
  id=null;

  constructor(private authservice : AuthService, private datos : DatosService, private modal :ModalController, public actionSheetController: ActionSheetController, private activeRoute: ActivatedRoute ){}

  onlogout(){
    this.authservice.logout();
  }

  ngOnInit(){
    this.datos.getDatos().subscribe( datos => {
      this.datosFire = datos;
    });
    this.id = this.activeRoute.snapshot.paramMap.get('id');
  }

  openDato(dato){
    this.modal.create({
      component: DatoComponent,
      componentProps : {
        dato: dato
      }
    }).then( (modal) => {
      modal.present()
    })
  }

  async presentActionSheet() {
    const actionSheet = await this.actionSheetController.create({
      header: 'Opciones',
      buttons: [{
        text: 'Salir',
        role: 'destructive',
        icon: 'log-out',
        handler: () => {
          this.onlogout();
        }
      }]
    });
    await actionSheet.present();
  }

}
